// Colours
$primary: #00BC77;
$secondary: #12002b;
$tertiary: #979797;
$cardColor: #fbfbfb;
$default: white;

// Typography
$fontWeight700: 700;
$fontWeight500: 500;
$fontWeight400: 400;
$fontSizeHigh: 1.35em;
$fontSizeLow: 1em;
$fontMontserrat: 'Montserrat', Arial, Helvetica, sans-serif;
$fontRoboto: 'Roboto', Arial, Helvetica, sans-serif;
$fontAvenir: 'Avenir', Arial, Helvetica, sans-serif;
$fontMSourceCodePro: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;

/// Break points
$breakpoints: (
        'small': (max-width: 768px),
        'medium': (max-width: 1024px),
        'medium-large': (max-width: 1280px),
        'large': (max-width: 1440px),
);

// SÃ©lectionner le format 'large'
$large_breakpoint: map-get($breakpoints, 'large');

// Border radius
$defaultSmallRadius: .35em;

// Animations
$defaultDurationAnimation: .3s;

// Layout
$spaceContent: 3.5em;
